    export default {
        name: 'app-shop',
	    props: {
            list: {
                type: Array,
	            default: function() {
	                return [
	                ]
	            }
            },
            navPicUrl: {
                type: String,
	            default: function() {
	                return "";
	            }
            },
            scorePicUrl: {
                type: String,
	            default: function() {
	                return "";
	            }
            },
            showName: {
                type: Boolean,
	            default: function() {
	                return true;
	            }
            },
            showScore: {
                type: Boolean,
	            default: function() {
	                return true;
	            }
            },
            showTel: {
                type: Boolean,
	            default: function() {
	                return true;
	            }
            },
            scrollTop: {
                type: Number,
	            default: function() {
	                return 0;
	            }
            },
            value: {
                type: Boolean,
	            default: function() {
	                return true;
	            }
            }
	    },
	    data() {
          return {
              height: 0,
	          request: true
          }
	    },
        created() {
            let that = this;
            uni.getSystemInfo({
                success: function (response) {
                    that.height = response.windowHeight;
                }
            })
        },
	    methods: {
          autoEnd() {
              this.$lazyLoadingData('app-shop').then(response => {
                  if (response[0].height + response[0].top < this.height) {
                      this.request = false;
                      this.$emit('input', this.request);
                  }
              });
          },
      },
	    watch: {
            scrollTop: {
                handler: function(n,o) {
                    if (n > o  &&  this.request) this.autoEnd();
                }
            }
	    },
        computed: {
            carCount() {
                return function (item) {
                    return new Array(item.score);
                }
            },
            newList() {
                this.list.forEach(item => {
                    if (!(item.score instanceof Array)) {
                        item.score = new Array(item.score);
                    }
                });
                return this.list;
            }
        }
    }
